.fotter_bar{
    position: fixed;
    height: 2.5rem;
    border-top: 1PX solid #ddd;
    width: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
}
.fotter_bar ul{
    display: -ms-flexbox;
    display: flex;
    height: 100%;
}
.fotter_bar ul li{
    -ms-flex: 1 1;
        flex: 1 1;
    -webkit-flex: 1;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.fotter_bar ul li a{
    display: block;
    height: 100%;
    font-size: 0.65rem;
    color: #555;
    padding-top: 0.25rem;
}
.fotter_bar ul li a .iconfont{
    font-size: 20px;
}
.fotter_bar ul li a p{
    margin-top: 0.1rem;
}


